<script>
	import ScatterChart from './ScatterChart.svelte';
	import { getData } from './utils.js';
	
	const regions = [
		{
			code: 'E12000001',
			name: 'North East'
		},
		{
			code: 'E12000002',
			name: 'North West'
		},
		{
			code: 'E12000003',
			name: 'Yorkshire and The Humber'
		},
		{
			code: 'E12000004',
			name: 'East Midlands'
		},
		{
			code: 'E12000005',
			name: 'West Midlands'
		},
		{
			code: 'E12000006',
			name: 'East of England'
		},
		{
			code: 'E12000007',
			name: 'London'
		},
		{
			code: 'E12000008',
			name: 'South East'
		},
		{
			code: 'E12000009',
			name: 'South West'
		}
	];
	const categories = regions.map(d => d.code);
	const colors = [
		[208, 88, 126],
		[218, 123, 145],
		[227, 156, 165],
		[234, 188, 185],
		[240, 220, 205],
		[230, 245, 208],
		[191, 228, 171],
		[151, 210, 135],
		[108, 192, 100],
		[55, 174, 63]
	];
	const datakeys = {
		imd: 'Overall',
		income: 'Income',
		employment: 'Employment',
		education: 'Education',
		health: 'Health',
		crime: 'Crime',
		housing: 'Housing',
		environment: 'Environment'
	}
	const url = 'https://raw.githubusercontent.com/bothness/geo-data/main/csv/imd-values-2019.csv';
	const catKey = 'region_code';
	
	let data;
	let places;
	let selected;
	let place;
	let region;
	let xKey = 'income';
	let yKey = 'employment';
	
	getData(url)
	.then(result => data = result)
	.then(() => {
		let codes = [];
		let array = [];
		data.forEach(d => {
			if (!codes.includes(d.lad_code)) {
				codes.push(d.lad_code);
				array.push({
					code: d.lad_code,
					name: d.lad_name
				});
			}
		});
		array.sort((a, b) => a.name.localeCompare(b.name));
		places = array;
	});
	
	function selectRegion() {
		selected = region;
		place = null;
	}
	function selectPlace() {
		selected = place;
		region = null;
	}
</script>

<style>
	:global(body) {
		background-color: #222;
		color: #ccc;
		padding: 0;
	}
	h1 {
		color: #eee;
	}
	select {
		max-width: 190px;
		margin-right: 10px;
	}
	#wrapper {
		margin: 0 20px 0 20px;
	}
	.label {
		display: inline-block;
		width: 50px;
		text-align: right;
		margin-right: 3px;
	}
	a {
		color: lightblue;
	}
</style>

<div id="wrapper">
<h1>IMD Scatter Plot Demo</h1>
{#if data}
<nobr><span class="label">X Axis</span>
<select bind:value={xKey}>
	{#each Object.keys(datakeys) as key}
		<option value={key}>{datakeys[key]}</option>
	{/each}
</select>
<span class="label">Y Axis</span>
<select bind:value={yKey}>
	{#each Object.keys(datakeys) as key}
		<option value={key}>{datakeys[key]}</option>
	{/each}
</select></nobr>

<nobr><span class="label">Region</span>
<!-- svelte-ignore a11y-no-onchange -->
<select bind:value={region} on:change={selectRegion}>
	<option value={null}>Select one</option>
	{#each regions as region}
		<option value={{value: region.code, col: 'region_code'}}>{region.name}</option>
	{/each}
</select>
{#if places}
<span class="label">District</span>
<!-- svelte-ignore a11y-no-onchange -->
<select bind:value={place} on:change={selectPlace}>
	<option value={null}>Select one</option>
	{#each places as place}
		<option value={{value: place.code, col: 'lad_code'}}>{place.name}</option>
	{/each}
</select>
{/if}
</nobr>

{#if xKey && yKey}
<ScatterChart diameter={4} {data} {xKey} {yKey} {catKey} {colors} {categories} {selected} />
{/if}
{/if}
<p><small>Data from the <a href="https://www.gov.uk/government/statistics/english-indices-of-deprivation-2019" target="_blank">2019 ONS Indices of Multiple Deprivation</a> (IMD) for 32,844 neighbourhoods (LSOAs) in England. Bottom-left = less deprived. Top-right = more deprived. Coloured by region. <a href="https://svelte.dev/repl/4202af21e6254c0baa7e967fb04d9efe?version=3.29.7" target="_blank">Get the source code</a>.</small></p>
</div>